@import "fonts.less";
@import "reset.less";


.container {
    width: 100%;
    margin: auto;
    padding: 0 120px;
}

.vintro {
    margin-top: 50px;
    &_row {
        display: flex;
        align-items: center;
    }
    &_text {
        &--title {
            font-size: 36px;
            line-height: 120%;
            .m();
        }
        &--desc {
            margin-top: 14px;
            font-size: 18px;
            line-height: 150%;
            color: #8C8C8C;
            max-width: 555px;
        }
        &--btn {
            margin-top: 30px;
            display: block;
            width: 300px;
            height: 50px;
            background: #CB4335;
            border-radius: 10px;
            line-height: 50px;
            color: #fff;
            font-size: 18px;
            text-align: center;
            transition: .4s all ease;
            &:hover {
                background: linear-gradient(180deg,#be0208 0,#a00d11 100%);
            }
        }
    }
    &_trig {
        margin-top: 55px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        max-width: 695px;
        &--item {
            margin-bottom: 35px;
            display: flex;
            align-items: center;
            width: 50%;
        }
        &--p {
            margin-left: 25px;
            font-size: 20px;
            line-height: 120%;
            color: #000;
        }
    }
}

.vdesc {
    margin-top: 150px;
    &_row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    &_text {
        &--title {
            font-size: 36px;
            line-height: 120%;
            .m();
        }
        &--desc {
            margin-top: 14px;
            font-size: 18px;
            line-height: 150%;
            color: #8C8C8C;
            max-width: 555px;
        }
    }
    &_trig {
        display: flex;
        flex-wrap: wrap;
        max-width: 860px;
        &--item {
            margin-bottom: 35px;
            display: flex;
            align-items: center;
            width: 50%;
        }
        &--p {
            margin-left: 25px;
            font-size: 20px;
            line-height: 120%;
            color: #000;
        }
    }
}

.vform {
    margin-top: 115px;
    background: url(../img/v-form.jpg);
    min-height: 300px;
    &_row {
        height: 300px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &_title {
        .m();
        font-size: 36px;
        line-height: 120%;
        color: #FFFFFF;
    }
    &_form {
        display: flex;
        align-items: center;
    }
    &_input {
        width: 335px;
        height: 50px;
        background: rgba(255, 255, 255, 0.35);
        border: 2px solid #FFFFFF;
        box-sizing: border-box;
        backdrop-filter: blur(10px);
        border-radius: 10px;
        font-size: 18px;
        color: #fff;
        padding: 0 30px;
        margin-right: 14px;
        &::placeholder {
            font-size: 18px;
            color: #fff;
        }
    }
    &_btn {
        display: block;
        width: 300px;
        height: 50px;
        background: #FFFFFF;
        border-radius: 10px;
        text-align: center;
        color: #000;
        font-size: 18px;
        line-height: 50px;
    }
}

.vcost {
    margin-top: 150px;
    &_title {
        font-size: 36px;
        line-height: 120%;
        .m();
    }
    &_row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-top: 50px;
    }
    &_item {
        &--top {
            display: flex;
            align-items: center;
        }
        &--red {
            font-size: 20px;
            line-height: 120%;
            color: #CB4335;
            margin-left: 20px;
        }
        &--desc {
            margin-top: 15px;
            font-size: 18px;
            line-height: 150%;
            max-width: 400px;
        }
    }
}

.vif {
    min-height: 765px;
    background: url(../img/v-img.jpg) right/contain no-repeat;
    &_block {
        min-height: 765px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    &_title {
        font-size: 36px;
        line-height: 120%;
        color: #000000;
        .m();
    }
    &_desc {
        margin-top: 15px;
        font-size: 18px;
        line-height: 150%;
        color: #8C8C8C;
        max-width: 670px;
    }
    &_more {
        margin-top: 15px;
        font-size: 18px;
        color: #CB4335;
        position: relative;
        cursor: pointer;
        width: 140px;
        &::after {
            content: '';
            position: absolute;
            top: 10px; right: 0;
            background: url(../img/vif-arrow.svg);
            width: 16px;
            height: 9px;
            transition: .4s all ease;
        }
        &.active::after {
            transform: rotate(180deg);
        }
    }
    &_bottom {
        margin-top: 60px;
        &--title {
            font-size: 20px;
            line-height: 120%;
            color: #000000;
        }
        &--row {
            margin-top: 18px;
            display: flex;
            max-width: 565px;
            align-items: center;
        }
        &--div {
            display: flex;
            align-items: center;
        }
        &--p {
            margin-left: 22px;
            font-size: 18px;
            color: #000000;
        }
    }
}

@media screen and (max-width:1600px) {
    .container {
        padding: 0 2rem;
    }
    .vcost_item--desc {
        padding-right: 20px;
    }
}

@media screen and (max-width:1500px) {
    .vintro {
        &_row {
            flex-direction: column;
        }
        &_text {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            &--desc {
                padding-right: 50px;
            }
        }
        &_trig {
            flex-direction: column;
            margin-top: 0;
            &--item {
                width: 100%;
            }
        }
    }
    .vdesc {
        margin-top: 100px;
        &_row {
            flex-direction: column;
        }
        &_text {
            &--desc {
                max-width: inherit;
            }
        }
        &_trig {
            margin-top: 50px;
            max-width: inherit;
        }
    }
    .vform {
        &_form {
            flex-direction: column;
        }
        &_title {
            max-width: 615px;
        }
        &_input {
            width: 300px;
            margin-right: 0;
            margin-bottom: 10px;
        }
        &_btn {
            margin-top: 5px;
        }
    }
    .vcost {
        &_row {
            flex-wrap: wrap;
            margin-top: 10px;
        }
        &_item {
            width: 50%;
            margin-top: 40px;
            padding-right:20px;
            &--desc {
                max-width: inherit;
            }
        }
    }
    .vif {
        background-position: bottom 0px right -200px;
    }
}

@media screen and (max-width:1350px) {
    .vif {
        background-position: bottom 0 right -159px;
        min-height: 458px;
        margin-top: 100px;
        &_block {
            min-height: 458px;
        }
    }
    .vintro {
        margin-top: 40px;
    }
    .vdesc {
        margin-top: 100px;
    }
    .vform {
        margin-top: 100px;
    }
    .vcost {
        margin-top: 100px;
    }
    .vif_bottom {
        margin-top: 40px;
    }
} 

@media screen and (max-width:1000px) {
    .vif {
        background: none;
    }
}

@media screen and (max-width:760px) {
    .vintro {
        &_text {
            flex-direction: column;   
        }
        &_trig {
            margin-top: 35px;
            &--item {
                margin-bottom: 20px;
            }
        }
    }
    .vdesc {
        &_trig {
          flex-direction: column;
            &--item {
                width: 100%;
            }
        }
    }
    .vform {
        padding: 65px 0;
        form {
            width: 100%;
        }
        &_row {
            height: auto;
            flex-direction: column;
        }
        &_form {
            margin-top: 30px;
        }
        &_input {
            width: 100%;
        }
        &_btn {
            width: 100%;
        }
    }
    .vcost {
        &_row {
            flex-direction: column;
        }
        &_item {
            width: 100%;
        }
    }
}

@media screen and (max-width:544px) {
    .container {
        padding: 0 15px;
    }
    .vintro {
        margin-top: 27px;
        &_text {
            &--title {
                font-size: 24px; 
            }
            &--desc {
                margin-top: 10px;
                font-size: 14px;
                line-height: 150%;
                padding-right: 0;
            }
            &--btn {
                margin-top: 15px;
                width: 100%;
            }
        }
        &_trig {
            margin-top: 0;
            &--item {
                margin-bottom: 0;
                margin-top: 25px;
            }
            &--p {
                margin-left: 20px;
                font-size: 18px;
                line-height: 120%;
            }
        }
        &_img {
            display: none;
        }
    }
    .vdesc {
        margin-top: 70px;
        &_text {
            &--title {
                font-size: 24px;
            }
            &--desc {
                margin-top: 10px;
                font-size: 14px;
                line-height: 150%;
            }
        }
        &_trig {
            margin-top: 0;
            &--item {
                margin: 25px 0 0;
            }
            &--p {
                margin-left: 10px;
                font-size: 18px;
                line-height: 120%;
            }
        }
    }
    .vform {
        margin-top: 70px;
        &_title {
            max-width: inherit;
            font-size: 24px;
            line-height: 130%;
        }
        &_btn {
            margin-top: 10px;
        }
    }
    .vcost {
        margin-top: 70px;
        &_title {
            font-size: 24px;
        }
        &_row {
            margin-top: 0;
        }
        &_item {
            margin-top: 35px;
            &--red {
                margin-left: 15px;
                font-size: 18px;
                line-height: 120%;
            }
            &--desc {
                font-size: 16px;
                padding-right: 0;
            }
        }
    }
    .vif {
        margin-top: 70px;
        &_title {
          font-size: 24px;  
        }
        &_desc {
            margin-top: 10px;
            font-size: 14px;
        }
        &_more {
            margin-top: 10px;
        }
        &_bottom {
            margin-top: 30px;
            &--title {
                font-size: 18px;
                line-height: 120%;
            }
            &--row {
                flex-direction: column;
                margin-top: 0;
            }
            &--div {
                margin-top: 22px;
            }
            &--p {
                margin-left: 17px;
                font-size: 16px;
            }
        }
    }
}