@font-face {
  font-family: 'Roboto';
  src: url('../fonts/RobotoRegular.eot');
  src: url('../fonts/RobotoRegular.eot') format('embedded-opentype'), url('../fonts/RobotoRegular.woff2') format('woff2'), url('../fonts/RobotoRegular.woff') format('woff'), url('../fonts/RobotoRegular.ttf') format('truetype'), url('../fonts/RobotoRegular.svg#RobotoRegular') format('svg');
}

@font-face {
  font-family: 'RobotoMedium';
  src: url('../fonts/RobotoMedium.eot');
  src: url('../fonts/RobotoMedium.eot') format('embedded-opentype'), url('../fonts/RobotoMedium.woff2') format('woff2'), url('../fonts/RobotoMedium.woff') format('woff'), url('../fonts/RobotoMedium.ttf') format('truetype'), url('../fonts/RobotoMedium.svg#RobotoMedium') format('svg');
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

body {
  font: 400 1.4rem/1.714 'Roboto', 'Helvetica', 'Arial', sans-serif;
  color: #343434;
}

ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

* {
  box-sizing: border-box;
}

.container {
  width: 100%;
  margin: auto;
  padding: 0 120px;
}

.vintro {
  margin-top: 50px;
}

.vintro_row {
  display: flex;
  align-items: center;
}

.vintro_text--title {
  font-size: 36px;
  line-height: 120%;
  font-family: 'RobotoMedium';
}

.vintro_text--desc {
  margin-top: 14px;
  font-size: 18px;
  line-height: 150%;
  color: #8C8C8C;
  max-width: 555px;
}

.vintro_text--btn {
  margin-top: 30px;
  display: block;
  width: 300px;
  height: 50px;
  background: #CB4335;
  border-radius: 10px;
  line-height: 50px;
  color: #fff;
  font-size: 18px;
  text-align: center;
  transition: .4s all ease;
}

.vintro_text--btn:hover {
  background: linear-gradient(180deg, #be0208 0, #a00d11 100%);
}

.vintro_trig {
  margin-top: 55px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 695px;
}

.vintro_trig--item {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  width: 50%;
}

.vintro_trig--p {
  margin-left: 25px;
  font-size: 20px;
  line-height: 120%;
  color: #000;
}

.vdesc {
  margin-top: 150px;
}

.vdesc_row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.vdesc_text--title {
  font-size: 36px;
  line-height: 120%;
  font-family: 'RobotoMedium';
}

.vdesc_text--desc {
  margin-top: 14px;
  font-size: 18px;
  line-height: 150%;
  color: #8C8C8C;
  max-width: 555px;
}

.vdesc_trig {
  display: flex;
  flex-wrap: wrap;
  max-width: 860px;
}

.vdesc_trig--item {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  width: 50%;
}

.vdesc_trig--p {
  margin-left: 25px;
  font-size: 20px;
  line-height: 120%;
  color: #000;
}

.vform {
  margin-top: 115px;
  background: url(../img/v-form.jpg);
  min-height: 300px;
}

.vform_row {
  height: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.vform_title {
  font-family: 'RobotoMedium';
  font-size: 36px;
  line-height: 120%;
  color: #FFFFFF;
}

.vform_form {
  display: flex;
  align-items: center;
}

.vform_input {
  width: 335px;
  height: 50px;
  background: rgba(255, 255, 255, 0.35);
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
  padding: 0 30px;
  margin-right: 14px;
}

.vform_input::placeholder {
  font-size: 18px;
  color: #fff;
}

.vform_btn {
  display: block;
  width: 300px;
  height: 50px;
  background: #FFFFFF;
  border-radius: 10px;
  text-align: center;
  color: #000;
  font-size: 18px;
  line-height: 50px;
}

.vcost {
  margin-top: 150px;
}

.vcost_title {
  font-size: 36px;
  line-height: 120%;
  font-family: 'RobotoMedium';
}

.vcost_row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 50px;
}

.vcost_item--top {
  display: flex;
  align-items: center;
}

.vcost_item--red {
  font-size: 20px;
  line-height: 120%;
  color: #CB4335;
  margin-left: 20px;
}

.vcost_item--desc {
  margin-top: 15px;
  font-size: 18px;
  line-height: 150%;
  max-width: 400px;
}

.vif {
  min-height: 765px;
  background: url(../img/v-img.jpg) right / contain no-repeat;
}

.vif_block {
  min-height: 765px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vif_title {
  font-size: 36px;
  line-height: 120%;
  color: #000000;
  font-family: 'RobotoMedium';
}

.vif_desc {
  margin-top: 15px;
  font-size: 18px;
  line-height: 150%;
  color: #8C8C8C;
  max-width: 670px;
}

.vif_more {
  margin-top: 15px;
  font-size: 18px;
  color: #CB4335;
  position: relative;
  cursor: pointer;
  width: 140px;
}

.vif_more::after {
  content: '';
  position: absolute;
  top: 10px;
  right: 0;
  background: url(../img/vif-arrow.svg);
  width: 16px;
  height: 9px;
  transition: .4s all ease;
}

.vif_more.active::after {
  transform: rotate(180deg);
}

.vif_bottom {
  margin-top: 60px;
}

.vif_bottom--title {
  font-size: 20px;
  line-height: 120%;
  color: #000000;
}

.vif_bottom--row {
  margin-top: 18px;
  display: flex;
  max-width: 565px;
  align-items: center;
}

.vif_bottom--div {
  display: flex;
  align-items: center;
}

.vif_bottom--p {
  margin-left: 22px;
  font-size: 18px;
  color: #000000;
}

@media screen and (max-width: 1600px) {
  .container {
    padding: 0 2rem;
  }

  .vcost_item--desc {
    padding-right: 20px;
  }
}

@media screen and (max-width: 1500px) {
  .vintro_row {
    flex-direction: column;
  }

  .vintro_text {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .vintro_text--desc {
    padding-right: 50px;
  }

  .vintro_trig {
    flex-direction: column;
    margin-top: 0;
  }

  .vintro_trig--item {
    width: 100%;
  }

  .vdesc {
    margin-top: 100px;
  }

  .vdesc_row {
    flex-direction: column;
  }

  .vdesc_text--desc {
    max-width: inherit;
  }

  .vdesc_trig {
    margin-top: 50px;
    max-width: inherit;
  }

  .vform_form {
    flex-direction: column;
  }

  .vform_title {
    max-width: 615px;
  }

  .vform_input {
    width: 300px;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .vform_btn {
    margin-top: 5px;
  }

  .vcost_row {
    flex-wrap: wrap;
    margin-top: 10px;
  }

  .vcost_item {
    width: 50%;
    margin-top: 40px;
    padding-right: 20px;
  }

  .vcost_item--desc {
    max-width: inherit;
  }

  .vif {
    background-position: bottom 0px right -200px;
  }
}

@media screen and (max-width: 1350px) {
  .vif {
    background-position: bottom 0 right -159px;
    min-height: 458px;
    margin-top: 100px;
  }

  .vif_block {
    min-height: 458px;
  }

  .vintro {
    margin-top: 40px;
  }

  .vdesc {
    margin-top: 100px;
  }

  .vform {
    margin-top: 100px;
  }

  .vcost {
    margin-top: 100px;
  }

  .vif_bottom {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1000px) {
  .vif {
    background: none;
  }
}

@media screen and (max-width: 760px) {
  .vintro_text {
    flex-direction: column;
  }

  .vintro_trig {
    margin-top: 35px;
  }

  .vintro_trig--item {
    margin-bottom: 20px;
  }

  .vdesc_trig {
    flex-direction: column;
  }

  .vdesc_trig--item {
    width: 100%;
  }

  .vform {
    padding: 65px 0;
  }

  .vform form {
    width: 100%;
  }

  .vform_row {
    height: auto;
    flex-direction: column;
  }

  .vform_form {
    margin-top: 30px;
  }

  .vform_input {
    width: 100%;
  }

  .vform_btn {
    width: 100%;
  }

  .vcost_row {
    flex-direction: column;
  }

  .vcost_item {
    width: 100%;
  }
}

@media screen and (max-width: 544px) {
  .container {
    padding: 0 15px;
  }

  .vintro {
    margin-top: 27px;
  }

  .vintro_text--title {
    font-size: 24px;
  }

  .vintro_text--desc {
    margin-top: 10px;
    font-size: 14px;
    line-height: 150%;
    padding-right: 0;
  }

  .vintro_text--btn {
    margin-top: 15px;
    width: 100%;
  }

  .vintro_trig {
    margin-top: 0;
  }

  .vintro_trig--item {
    margin-bottom: 0;
    margin-top: 25px;
  }

  .vintro_trig--p {
    margin-left: 20px;
    font-size: 18px;
    line-height: 120%;
  }

  .vintro_img {
    display: none;
  }

  .vdesc {
    margin-top: 70px;
  }

  .vdesc_text--title {
    font-size: 24px;
  }

  .vdesc_text--desc {
    margin-top: 10px;
    font-size: 14px;
    line-height: 150%;
  }

  .vdesc_trig {
    margin-top: 0;
  }

  .vdesc_trig--item {
    margin: 25px 0 0;
  }

  .vdesc_trig--p {
    margin-left: 10px;
    font-size: 18px;
    line-height: 120%;
  }

  .vform {
    margin-top: 70px;
  }

  .vform_title {
    max-width: inherit;
    font-size: 24px;
    line-height: 130%;
  }

  .vform_btn {
    margin-top: 10px;
  }

  .vcost {
    margin-top: 70px;
  }

  .vcost_title {
    font-size: 24px;
  }

  .vcost_row {
    margin-top: 0;
  }

  .vcost_item {
    margin-top: 35px;
  }

  .vcost_item--red {
    margin-left: 15px;
    font-size: 18px;
    line-height: 120%;
  }

  .vcost_item--desc {
    font-size: 16px;
    padding-right: 0;
  }

  .vif {
    margin-top: 70px;
  }

  .vif_title {
    font-size: 24px;
  }

  .vif_desc {
    margin-top: 10px;
    font-size: 14px;
  }

  .vif_more {
    margin-top: 10px;
  }

  .vif_bottom {
    margin-top: 30px;
  }

  .vif_bottom--title {
    font-size: 18px;
    line-height: 120%;
  }

  .vif_bottom--row {
    flex-direction: column;
    margin-top: 0;
  }

  .vif_bottom--div {
    margin-top: 22px;
  }

  .vif_bottom--p {
    margin-left: 17px;
    font-size: 16px;
  }
}